@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@400;500;700&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}
/* width  */
::-webkit-scrollbar {
  width: 8px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px transparent;
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: darkgray;
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: grey;
}

body {
  background-color: whitesmoke;
  color: #000;
  font-family: 'Ubuntu', sans-serif;
  overflow-x: hidden;
}

.header_app {
  display: flex;
  justify-content: space-between;
  width: 100vw;
  height: 3rem;
  padding: 0.5rem 1rem;
}

.logo_name {
  font-size: 1.5rem;
  color: #000;
}

.username {
  text-decoration: none;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.username p {
  color: crimson;
  font-weight: bold;
  font-size: 1.2rem;
}

.menu {
  border-top: 1px solid rgb(185, 184, 184);
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100vw;
  padding: 0.5rem 1rem;
  background-color: white;
}

.menu ul {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.menu ul li {
  list-style: none;
  margin-left: 1rem;
}

.menu ul li a {
  text-decoration: none;
  color: #6d6a63;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  cursor: pointer;
}

.menu ul li a.active {
  color: #000;
  font-weight: bolder;
}

@media (min-width: 820px) {
  .options {
    display: flex;
    justify-content: space-around;
    width: 50%;
    max-width: 500px;
  }
  .menu {
    border-top: none;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0.5rem 1rem;
    width: 80%;
    background-color: transparent;
  }

  .menu ul {
    width: 100%;
  }

  .menu ul li a {
    padding: 0.5rem 0;
  }

  .menu ul li a:hover {
    border-bottom: 2px solid crimson;
  }

  .menu ul li a.active {
    border-bottom: 2px solid crimson;
  }

  .menu ul li a p {
    font-size: 1.2rem;
  }

  .menu ul li a i {
    display: none;
  }
}

.balance_sheet {
  width: 80%;
  max-width: 400px;
  margin: 1rem auto 1rem;
  display: flex;
  flex-direction: column;
  border-bottom: 1px solid rgb(189, 186, 186);
}

.balance h1 {
  font-size: 2.5rem;
  font-weight: bolder;
  margin-bottom: 0.5rem;
  text-align: center;
}

.balance p {
  font-size: 0.8rem;
  text-align: center;
  color: grey;
}

.data {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin: 1rem auto;
}

.income,
.outcome {
  width: 50%;
  text-align: center;
  height: 100%;
}

.income p,
.outcome p {
  color: grey;
}

.income h3 {
  color: #0fdd65;
  font-size: 1.2rem;
}

.outcome h3 {
