.transaction {
  border-radius: 5px;
  background-color: white;
  margin: 0 auto;
  margin-bottom: 1rem;
  box-shadow: 1px 1px 2px;
}

.transaction a {
  text-decoration: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2.3rem 1.2rem;
  width: 330px;
  height: 20px;
}

.transaction a .transaction_category {
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  color: white;
  margin-right: 0.5rem;
}

.transaction a .name_date {
  width: 80%;
  display: flex;
  justify-content: space-between;
}

.transaction a .name_date div {
  display: flex;
  flex-direction: column;
  height: 30px;
}

.transaction a .name_date div h4 {
  color: rgb(44, 42, 42);
  font-weight: bold;
}

.transaction a .name_date div p {
  color: rgb(189, 189, 189);
  font-size: 0.9rem;
}

